.note-container{
    height: 100%;
}

.note-container >h2{
    margin: 0;
    padding: 10px 0;
    font-size: 2.5rem;
    font-weight: 900;
}

.note-container-notes{
    height: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 20px;
    overflow-y: scroll;
}